.card{

    width:400px;
    min-height:800px;
    border-radius: 12px;
    border: 5px solid;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin:20px;
    cursor: grab;
    font-size: 26px;  
}



@media (min-width: 0px) and (max-width: 900px) {
    .card {
        font-size: 16px;
        margin:7px;
    }
  }
  @media (min-width: 0px) and (max-width: 600px) {
    .card {
        min-height:300px;
    }
  }
  @media (min-width: 0px) and (max-width: 600px) {
    .card {
        font-size: 26px;
    }
  }