* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    max-width: 100%;
    
  
  }

  .App {
    display: flex;
    min-width: 100%;
    height: 100vh;
    justify-content: center;
    align-items: center;
  
  }

  @media (min-width: 0px) and (max-width: 600px) {
    .App {
        flex-wrap: wrap;
    }
  }
  

